import { Checkbox, FormControlLabel, FormGroup, Grid, Typography } from '@mui/material';
import React from 'react';


function SideBar(props) {
    return (
        <>
             <Grid item={true} xs={12} sm={6} md={3}>
              <Typography variant="h5" gutterBottom>
                Hng sn xut
              </Typography>
              <Grid container>
                <Grid item={true} md={6} xs={12} sm={12}>
                  <FormGroup>
                    <FormControlLabel
                      control={<Checkbox defaultChecked />}
                      label="Tt c"
                    />
                    <FormControlLabel control={<Checkbox />} label="Sam sung" />
                    <FormControlLabel control={<Checkbox />} label="Vivo" />
                    <FormControlLabel control={<Checkbox />} label="Xiaomi" />
                  </FormGroup>
                </Grid>
                <Grid item={true} md={6} xs={12} sm={12}>
                  <FormGroup>
                    <FormControlLabel
                      control={<Checkbox />}
                      label="Apple(Iphone)"
                    />
                    <FormControlLabel control={<Checkbox />} label="Oppo" />
                    <FormControlLabel control={<Checkbox />} label="Nokia" />
                  </FormGroup>
                </Grid>
                <Grid container>
                  <FormGroup>
                    <Typography variant="h5" gutterBottom>
                      Mc gi
                    </Typography>
                    <FormControlLabel
                      control={<Checkbox defaultChecked />}
                      label="Tt c"
                    />
                    <FormControlLabel
                      control={<Checkbox />}
                      label="Di 2 triu"
                    />
                    <FormControlLabel
                      control={<Checkbox />}
                      label="T 2-4 triu"
                    />
                    <FormControlLabel
                      control={<Checkbox />}
                      label="T 4-7 triu"
                    />
                    <FormControlLabel
                      control={<Checkbox />}
                      label="T 7-13 triu"
                    />
                    <FormControlLabel
                      control={<Checkbox />}
                      label="Trn 13 triu"
                    />
                  </FormGroup>
                </Grid>
              </Grid>
            </Grid>
        </>
    );
}

export default SideBar;